<template>
  <div class="resume-job-intension" v-if="jobIntension">
    <h4 class="resume-job-intension__title text-center mb-3">求职意向</h4>
    <ul class="resume-job-intension__content list-unstyled">
      <li>{{ jobIntension.position }}</li>
      <li>{{ jobIntension.location }}</li>
      <li>{{ jobIntension.salary }}</li>
      <li>{{ jobIntension.jobTime }}</li>
    </ul>
  </div>
  <div class="resume-job-intension" v-else>
    <content-placeholders :rounded="false">
      <content-placeholders-img />
    </content-placeholders>
  </div>
</template>

<script>
export default {
  name: 'ResumeJobIntension',
  props: ['jobIntension'],
  components: {
    //
  }
}
</script>

<style lang="scss" >
  .resume-job-intension {
    background-color: #fff;
    padding: 1rem 2rem;
  }
  .resume-job-intension__content{
    display: flex;
    flex-direction: row;
    justify-content: center;
    >li{
      padding:5px 10px;
    }
  }
</style>



